import "core-js/modules/es.array.includes.js";
export default {
  data() {
    return {
      email: "",
      message: "",
      isValid: true
    };
  },

  methods: {
    onSubmit() {
      this.isValid = true;

      if (this.email === "" || this.message === "" || !this.email.includes("@")) {
        this.isValid = false;
        return;
      }

      this.$store.dispatch("requests/contactMentor", {
        email: this.email,
        message: this.message,
        mentorId: this.$route.params.id
      });
      this.$router.replace("/mentors");
    }

  }
};