export default {
  emits: ["update-filter"],

  data() {
    return {
      filters: {
        frontend: true,
        backend: true,
        career: true
      }
    };
  },

  methods: {
    setFilter(e) {
      const id = e.target.id;
      const isChecked = e.target.checked;
      const updatedFilters = { ...this.filters,
        [id]: isChecked
      };
      this.filters = updatedFilters;
      this.$emit("update-filter", updatedFilters);
    }

  }
};