export default {
  props: {
    id: String,
    firstName: String,
    lastName: String,
    rate: Number,
    areas: Array,
    isLoggedIn: Boolean
  },
  computed: {
    mentorId() {
      return this.$store.getters.userId !== this.id;
    },

    isLogged() {
      return this.isLoggedIn;
    },

    fullName() {
      return `${this.firstName} ${this.lastName}`;
    },

    mentorContactLink() {
      return `${this.$route.path}/${this.id}/contact`; // incase route will change
    },

    mentorDetailsLink() {
      return `${this.$route.path}/${this.id}`;
    }

  }
};