import "core-js/modules/es.array.includes.js";
export default {
  data() {
    return {
      email: "",
      password: "",
      formIsValid: true,
      mode: "login",
      error: false
    };
  },

  computed: {
    submitButtonText() {
      if (this.mode === "login") {
        return "Login";
      } else {
        return "SignUp";
      }
    },

    switchModeButtonText() {
      if (this.mode === "login") {
        return "Signup instead";
      } else {
        return "Login instead";
      }
    }

  },
  methods: {
    async onSubmit() {
      this.formIsValid = true;

      if (this.email === "" || !this.email.includes("@") || this.password.length < 8) {
        this.formIsValid = false;
        return;
      }

      try {
        if (this.mode === "login") {
          await this.$store.dispatch("login", {
            email: this.email,
            password: this.password
          });
        } else {
          await this.$store.dispatch("signup", {
            email: this.email,
            password: this.password
          });
        }

        this.$router.replace("/mentors");
      } catch (error) {
        this.error = error;
        alert("User with such email already exists");
        this.$router.replace("/auth");
      }
    },

    switchAuthMode() {
      if (this.mode === "login") {
        this.mode = "signup";
      } else {
        this.mode = "login";
      }
    }

  }
};